</div></demo-block>
<h2 id="biao-ti-he-nei-rong-zhi-chi-html"><a class="header-anchor" href="#biao-ti-he-nei-rong-zhi-chi-html" aria-hidden="true">¶</a> 标题和内容支持html</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;xl-poptip dangerous-html title=\&quot;&lt;h1&gt;这是加粗的标题&lt;/h1&gt;\&quot; content=\&quot;&lt;b&gt;这是加粗的内容&lt;/b&gt;\&quot;&gt;\n    &lt;xl-button&gt;富文本&lt;/xl-button&gt;\n&lt;/xl-poptip&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                            <div><xl-poptip dangerous-html="" title="<h1>这是加粗的标题</h1>" content="<b>这是加粗的内容</b>">
    <xl-button>富文本</xl-button>
</xl-poptip>
</div>
                            <div slot="desc"><p>通过指定<code>dangerousHtml</code>支持HTML</p>
</div>
                            <div slot="source"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">xl-poptip</span> <span class="hljs-attr">dangerous-html</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"&lt;h1&gt;这是加粗的标题&lt;/h1&gt;"</span> <span class="hljs-attr">content</span>=<span class="hljs-string">"&lt;b&gt;这是加粗的内容&lt;/b&gt;"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-button</span>&gt;</span>富文本<span class="hljs-tag">&lt;/<span class="hljs-name">xl-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">xl-poptip</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="bu-xian-shi-jian-tou"><a class="header-anchor" href="#bu-xian-shi-jian-tou" aria-hidden="true">¶</a> 不显示箭头</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;xl-poptip :arrow=\&quot;false\&quot; content=\&quot;箭头不会显示\&quot;&gt;\n    &lt;xl-button&gt;自定义内容&lt;/xl-button&gt;\n&lt;/xl-poptip&gt;\n\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                            <div><xl-poptip :arrow="false" content="箭头不会显示">
    <xl-button>自定义内容</xl-button>
</xl-poptip>

</div>
                            <div slot="desc"><p>通过<code>arrow</code>属性配置</p>
</div>
                            <div slot="source"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">xl-poptip</span> <span class="hljs-attr">:arrow</span>=<span class="hljs-string">"false"</span> <span class="hljs-attr">content</span>=<span class="hljs-string">"箭头不会显示"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-button</span>&gt;</span>自定义内容<span class="hljs-tag">&lt;/<span class="hljs-name">xl-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">xl-poptip</span>&gt;</span>

</code></pre>
</div></demo-block>
<h2 id="zhi-ding-ji-huo-fang-shi"><a class="header-anchor" href="#zhi-ding-ji-huo-fang-shi" aria-hidden="true">¶</a> 指定激活方式</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;div class=\&quot;demo-poptip\&quot;&gt;\n    &lt;xl-poptip content=\&quot;点击激活\&quot; trigger=\&quot;click\&quot;&gt;\n        &lt;xl-button&gt;点击激活&lt;/xl-button&gt;\n    &lt;/xl-poptip&gt;\n&lt;/div&gt;\n&lt;div class=\&quot;demo-poptip\&quot;&gt;\n    &lt;xl-poptip content=\&quot;悬停激活\&quot; trigger=\&quot;hover\&quot;&gt;\n        &lt;xl-button&gt;悬停激活&lt;/xl-button&gt;\n    &lt;/xl-poptip&gt;\n&lt;/div&gt;\n&lt;xl-poptip content=\&quot;自定义激活\&quot; v-model=\&quot;visiable\&quot; trigger=\&quot;custom\&quot;&gt;\n    &lt;xl-checkbox v-model=\&quot;visiable\&quot;&gt;自定义激活&lt;/xl-checkbox&gt;\n&lt;/xl-poptip&gt;\n\n\n\n&quot;,&quot;script&quot;:&quot;\n    export default{\n        data(){\n            return{\n                visiable:false\n            }\n        }\n    }\n&quot;,&quot;style&quot;:&quot;\n    .demo-poptip{\n        margin-bottom:2em;\n    }\n&quot;}">
                            <div><div class="demo-poptip">
    <xl-poptip content="点击激活" trigger="click">
        <xl-button>点击激活</xl-button>
    </xl-poptip>
</div>
<div class="demo-poptip">
    <xl-poptip content="悬停激活" trigger="hover">
        <xl-button>悬停激活</xl-button>
    </xl-poptip>
</div>
<xl-poptip content="自定义激活" v-model="visiable" trigger="custom">
    <xl-checkbox v-model="visiable">自定义激活</xl-checkbox>
</xl-poptip>



</div>
                            <div slot="desc"><p>通过指定<code>trigger</code>改变激活方式</p>
</div>
                            <div slot="source"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-poptip"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-poptip</span> <span class="hljs-attr">content</span>=<span class="hljs-string">"点击激活"</span> <span class="hljs-attr">trigger</span>=<span class="hljs-string">"click"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">xl-button</span>&gt;</span>点击激活<span class="hljs-tag">&lt;/<span class="hljs-name">xl-button</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">xl-poptip</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-poptip"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-poptip</span> <span class="hljs-attr">content</span>=<span class="hljs-string">"悬停激活"</span> <span class="hljs-attr">trigger</span>=<span class="hljs-string">"hover"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">xl-button</span>&gt;</span>悬停激活<span class="hljs-tag">&lt;/<span class="hljs-name">xl-button</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">xl-poptip</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">xl-poptip</span> <span class="hljs-attr">content</span>=<span class="hljs-string">"自定义激活"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"visiable"</span> <span class="hljs-attr">trigger</span>=<span class="hljs-string">"custom"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-checkbox</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"visiable"</span>&gt;</span>自定义激活<span class="hljs-tag">&lt;/<span class="hljs-name">xl-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">xl-poptip</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span>{
        data(){
            <span class="hljs-keyword">return</span>{
                <span class="hljs-attr">visiable</span>:<span class="hljs-literal">false</span>
            }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">style</span>&gt;</span><span class="css">
    <span class="hljs-selector-class">.demo-poptip</span>{
        <span class="hljs-attribute">margin-bottom</span>:<span class="hljs-number">2em</span>;
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">style</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="zi-ding-yi-nei-rong"><a class="header-anchor" href="#zi-ding-yi-nei-rong" aria-hidden="true">¶</a> 自定义内容</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;xl-poptip&gt;\n    &lt;xl-button&gt;自定义内容&lt;/xl-button&gt;\n    &lt;xl-input type=\&quot;textarea\&quot; slot=\&quot;content\&quot;&gt;&lt;/xl-input&gt;\n&lt;/xl-poptip&gt;\n\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                            <div><xl-poptip>
    <xl-button>自定义内容</xl-button>
    <xl-input type="textarea" slot="content"></xl-input>
</xl-poptip>

</div>
                            <div slot="desc"><p>通过slot <code>content</code>自定义内容</p>
</div>
                            <div slot="source"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">xl-poptip</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-button</span>&gt;</span>自定义内容<span class="hljs-tag">&lt;/<span class="hljs-name">xl-button</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-input</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"textarea"</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"content"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-input</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">xl-poptip</span>&gt;</span>

</code></pre>
</div></demo-block>
<h2 id="duo-ji-qian-tao"><a class="header-anchor" href="#duo-ji-qian-tao" aria-hidden="true">¶</a> 多级嵌套</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;xl-poptip padding=\&quot;0\&quot;&gt;\n    &lt;xl-button&gt;按钮1&lt;/xl-button&gt;\n    &lt;xl-poptip slot=\&quot;content\&quot; padding=\&quot;0\&quot;&gt;\n        &lt;xl-button&gt;按钮2&lt;/xl-button&gt;\n        &lt;xl-poptip slot=\&quot;content\&quot; padding=\&quot;0\&quot;&gt;\n            &lt;xl-button&gt;按钮3&lt;/xl-button&gt;\n            &lt;xl-button slot=\&quot;content\&quot;&gt;按钮3&lt;/xl-button&gt;\n        &lt;/xl-poptip&gt;\n    &lt;/xl-poptip&gt;\n&lt;/xl-poptip&gt;\n\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                            <div><xl-poptip padding="0">
    <xl-button>按钮1</xl-button>
    <xl-poptip slot="content" padding="0">
        <xl-button>按钮2</xl-button>
        <xl-poptip slot="content" padding="0">
            <xl-button>按钮3</xl-button>
            <xl-button slot="content">按钮3</xl-button>
        </xl-poptip>
    </xl-poptip>
</xl-poptip>

</div>
                            <div slot="desc"><p>多级嵌套，基于此可以实现类似菜单栏的结构</p>
</div>
                            <div slot="source"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">xl-poptip</span> <span class="hljs-attr">padding</span>=<span class="hljs-string">"0"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-button</span>&gt;</span>按钮1<span class="hljs-tag">&lt;/<span class="hljs-name">xl-button</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-poptip</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"content"</span> <span class="hljs-attr">padding</span>=<span class="hljs-string">"0"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">xl-button</span>&gt;</span>按钮2<span class="hljs-tag">&lt;/<span class="hljs-name">xl-button</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">xl-poptip</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"content"</span> <span class="hljs-attr">padding</span>=<span class="hljs-string">"0"</span>&gt;</span>
            <span class="hljs-tag">&lt;<span class="hljs-name">xl-button</span>&gt;</span>按钮3<span class="hljs-tag">&lt;/<span class="hljs-name">xl-button</span>&gt;</span>
            <span class="hljs-tag">&lt;<span class="hljs-name">xl-button</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"content"</span>&gt;</span>按钮3<span class="hljs-tag">&lt;/<span class="hljs-name">xl-button</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">xl-poptip</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">xl-poptip</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">xl-poptip</span>&gt;</span>

</code></pre>
</div></demo-block>
<h2 id="poptip-shu-xing"><a class="header-anchor" href="#poptip-shu-xing" aria-hidden="true">¶</a> Poptip 属性</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>transfer</td>
<td>是否将弹出框置于body中</td>
<td>boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>placement</td>
<td>弹出框相对显示框的位置</td>
<td>string</td>
<td>top/top-start/top-end/bottom/bottom-start/bottom-end/left/left-start/left-end/right/right-start/right-end</td>
<td>bottom</td>
</tr>
<tr>
<td>trigger</td>
<td>激活方式</td>
<td>string</td>
<td>click/hover/custom</td>
<td>hover</td>
</tr>
<tr>
<td>width</td>
<td>弹出框宽,通过设置{min:10,max:20}来设置最小宽度和最大宽度</td>
<td>string/number/object</td>
<td>-</td>
<td>{min: 150,max: 300}</td>
</tr>
<tr>
<td>value</td>
<td>弹出框状态，支持<code v-pre="">v-model</code></td>
<td>boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>title</td>
<td>标题</td>
<td>string</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>content</td>
<td>内容</td>
<td>string</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>padding</td>
<td>自定义边距</td>
<td>string</td>
<td>-</td>
<td>'0.5em,1em'</td>
</tr>
<tr>
<td>options</td>
<td>自定义popper.js的配置项，具体配置见<a href="https://popper.js.org/popper-documentation.html">popper.js文档</a></td>
<td>string</td>
<td>-</td>
<td>'0.5em,1em'</td>
</tr>
<tr>
<td>pop-class</td>
<td>自定义弹出框样式</td>
<td>string</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>transition-name</td>
<td>自定义弹出框动画</td>
<td>string</td>
<td>-</td>
<td>xl-poptip-fade</td>
</tr>
<tr>
<td>dangerous-html</td>
<td>允许嵌入HTML</td>
<td>boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>arrow</td>
<td>是否显示箭头</td>
<td>boolean</td>
<td>-</td>
<td>true</td>
</tr>
</tbody>
</table>
<h2 id="poptip-shi-jian"><a class="header-anchor" href="#poptip-shi-jian" aria-hidden="true">¶</a> Poptip 事件</h2>
<table class="table"><thead>
<tr>
<th>名称</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>on-popper-show</td>
<td>在提示框显示时触发</td>
<td>-</td>
</tr>
<tr>
<td>on-popper-hide</td>
<td>在提示框消失时触发</td>
<td>-</td>
</tr>
<tr>
<td>on-popper-created</td>
<td>popper创建完成时触发</td>
<td>this</td>
</tr>
<tr>
<td>on-popper-updated</td>
<td>popper更新完成时触发</td>
<td>this</td>
</tr>
<tr>
<td>on-input</td>
<td>弹出框状态改变时触发</td>
<td>boolean，true：弹出框显示，false：弹出框隐藏</td>
</tr>
<tr>
<td>on-click</td>
<td>弹出框附着的元素被点击时触发</td>
<td>-</td>
</tr>
<tr>
<td>on-clickoutside</td>
<td>弹出框外点击时触发</td>
<td>实际被点击的元素e</td>
</tr>
<tr>
<td>on-mouseenter</td>
<td>鼠标进入组件时触发</td>
<td>-</td>
</tr>
<tr>
<td>on-mouseleave</td>
<td>鼠标离开组件时触发</td>
<td>-</td>
</tr>
</tbody>
</table>
<h2 id="poptip-slots"><a class="header-anchor" href="#poptip-slots" aria-hidden="true">¶</a> Poptip slots</h2>
<table class="table"><thead>
<tr>
<th>名称</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>default</td>
<td>插入显示框</td>
</tr>
<tr>
<td>title</td>
<td>弹出框标题，定义此 slot 时，会覆盖 props <code v-pre="">title</code></td>
</tr>
<tr>
<td>content</td>
<td>弹出框内容，定义此 slot 时，会覆盖 props <code v-pre="">content</code></td>
</tr>
</tbody>
</table>
</section></template>
<style>
    .demo-poptip{
        margin-bottom:2em;
    }
</style>
<script>
    export default{
        data(){
            return{
                visiable:false
            }
        }
    }
</script>


